import Api from '@/common/api';
import share from '@/components/share';
import { mapState } from 'vuex';
export default {
	components: {
		share
	},
	data() {
		return {
			specClass: 'none',
			specSelected: [],
			small: null,
			sku: [],
			detailData: [],
			favorite: true,
			shareList: [],

			desc: `

				`,
			skuList: []
		};
	},
	async onLoad(ops) {
		//接收传值,id里面放的是标题，因为测试数据并没写id
		let id = ops.id;

		if (id) {
			this.logining = true;
			let params = { id: ops.id };
			let data = await Api.apiCall('get', Api.cms.subjectDetail, params);
			this.logining = false;

			if (data) {
				let detailData = data;
				this.detailData = detailData;
				this.desc = data.content;
				uni.setNavigationBarTitle({
					title: detailData.name
				});
			}
		}

		this.shareList = await this.$api.json('shareList');
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		//收藏
		toFavorite(item) {
			if (!this.hasLogin) {
				let url = '/pages/public/login';
				uni.navigateTo({
					url
				});
			} else {
				this.favorite = !this.favorite;
				let params = { objId: item.id, type: 1, name: item.name, meno1: item.pic };
				Api.apiCall('post', Api.goods.favoriteSave, params);
			}
		},
		//分享
		share() {
			this.$refs.share.toggleMask();
		},
		//收藏
		toFavorite(item) {
			if (!this.hasLogin) {
				let url = '/pages/public/login';
				uni.navigateTo({
					url
				});
			} else {
				this.favorite = !this.favorite;
				let params = { objId: item.id, type: 4, name: item.name, meno1: item.pic, meno2: item.price, meno3: item.sale };
				Api.apiCall('post', Api.goods.favoriteSave, params);
			}
		},

		async jifenPay(item) {
			if (!this.hasLogin) {
				let url = '/pages/public/login';
				uni.navigateTo({
					url
				});
				return false;
			}

			let id = item.id;
			let data;

			let params = { goodsId: id };
			data = await Api.apiCall('post', Api.order.jifenPay, params);

			if (data) {
				this.$api.msg(data);
			}
		},
		stopPrevent() {}
	}
};
